.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;               /* make header stick to the top */
    top:0;
    z-index: 100;                   /* height of sticky */
    background-color: white;      /* dont want transparent */
}

.header__logo {
   height: 25px; 
   object-fit: contain;
   margin-left: 20px;
}

.header__input {
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid black;
}
.header__inputButton {

    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}
.header__input > input {
    flex: 1;
    border: none;
}
.header__icons {
    display: flex;
    align-items: center;
}

.header__icon {
    /* toprights icons more margin */
    margin-right: 8px;
}